class AppComponent extends Component {
  render() {
    return (
      <div className="container index">
        <Header name={name}/>
        <Jumbo react={react} ufc={ufc} vs={vs}/>

        <div className="app row">
          <div className="col-xs-6">
            <List title='PPV' events={events.filter((event)=> event.subtitle==='Live on Pay-Per-View')}/>
            <List title='FS1 Shows' events={events.filter((event)=> event.subtitle==='FS1')}/>
          </div>
          <div className="col-xs-6">
            <List title='FS1 Live Shows' events={events.filter((event)=> event.subtitle==='Live on FS1')}/>
            <List title='Fight Pass Shows' events={events.filter((event)=> event.subtitle==='Live on UFC FIGHT PASS')}/>
          </div>
        </div>
        <FightersAppContainer />
      </div>
    );
  }
}

AppComponent.defaultProps = {
};

export default AppComponent;



