class FightersAppContainer extends Component {
  constructor(){
    super();
    this.state={
      fighters: []
    };
  }

  componentDidMount(){
    fetch('./fighters.json')
    .then((response) => response.json())
    .then((responseData) => {
      this.setState({fighters: responseData});
    })
    .catch((error) => {
      console.log('Error fetching and parsing data', error);
    });
  }

  render(){
    return (
      <FighterApp fighters={this.state.fighters} />
    );
  }
}

class FighterApp extends Component {

  constructor() {
    super();
    this.state = {
      filterText: ``
    }
  }

  //you create a local function to change the filterText state and pass this function down as a prop to the searchBar
  handleUserInput(searchTerm) {
    this.setState({filterText: searchTerm});
  }

  render() {
    return (
      <div>
        <SearchBar filterText={this.state.filterText} onUserInput={this.handleUserInput.bind(this)} />
        <FighterList fighters={fighters} filterText={this.state.filterText} />
      </div>
    );
  }
}

FighterApp.propTypes = {
  fighters: PropTypes.arrayOf(PropTypes.object)
}

export default FightersAppContainer;



